  }, [_c('p', [_vm._v("“秋是一个歌，但是‘桂花蒸’的夜，像在厨里吹的箫调，白天像小孩子唱的歌，又热又熟又清又湿。”")]), _vm._v(" "), _c('p', [_vm._v("——炎樱丁阿小手牵着儿子百顺，一层一层楼爬上来。高楼的后阳台上望出去，城市成了旷野，苍苍的无数的红的灰的屋脊，都是些后院子，后窗，后巷堂，连天也背过脸去了，无面目的阴阴的一片，过了八月节还这么热，也不知它是什么心思。")]), _vm._v(" "), _c('p', [_vm._v("下面浮起许多声音，各样的车，拍拍打地毯，学校*R*R摇铃，工匠捶着锯着，马达嗡嗡响，但都恍惚得很，似乎都不在上帝心上，只是耳旁风。")]), _vm._v(" "), _c('p', [_vm._v("公寓中对门邻居的阿妈带着孩子们在后阳台上吃粥，天太热，粥太烫，撮尖了嘴唇凋嗤凋嗤吹着，眉心紧皱，也不知是心疼自己的嘴唇还是心疼那雪白的粥。对门的阿妈是个黄脸婆，半大脚，头发却是剪了的。她忙着张罗孩子们吃了早饭上学去，她耳边挂下细细一绺子短发，湿腻腻如同墨画在脸上的还没干。她和阿小招呼：“早呀，妹妹！”孩子们纷纷叫：“阿姨，早！”阿小叫还一声“阿姐！”百顺也叫：“阿姨！阿哥！”")]), _vm._v(" "), _c('p', [_vm._v("阿小说：“今天来晚了——断命电车轧得要死，走过头了才得下来。外国人一定揿过铃了！”对门阿妈道：“这天可是发痴，热得这样！”阿小也道：“真发痴！都快到九月了呀！”")]), _vm._v(" "), _c('p', [_vm._v("刚才在三等电车上，她被挤得站立不牢，脸贴着一个高个子人的蓝布长衫，那深蓝布因为肮脏到极点，有一种奇异的柔软，简直没有布的劲道；从那蓝布的深处一蓬一蓬慢慢发出它内在的热气。这天气的气味也就像那袍子——而且绝对不是自己的衣服，自己的脏又还脏得好些。")]), _vm._v(" "), _c('p', [_vm._v("阿小急急用钥匙开门进去，先到电铃盒子前面一看，果然，二号的牌子掉了下来了。主人昨天没在家吃晚饭，让她早两个钟头回去，她猜着他今天要特别的疙瘩，作为补偿。她揭开水缸的盖，用铁匙子舀水，灌满一壶，放在煤气炉上先烧上了。战时自来水限制，家家有这样一个缸，酱黄大水缸上面描出淡黄龙。女人在那水里照见自己的影子，总像是古美人，可是阿小是个都市女性，她宁可在门边绿粉墙上粘贴着的一只缺了角的小粉镜"), _c('span', {
    staticClass: "quote"
  }, [_vm._v("（本来是个皮包的附属品）")]), _vm._v("里面照了一照，看看头发，还不很毛。她梳辫子头，脑后的头发一小股一小股恨恨地扭在一起，扭绞得它完全看不见了为止，方才觉得清爽相了。额前照时新的样式做得高高的，做得紧，可以三四天梳一梳。她在门背后取下白围裙来系上，端过凳子，踩在上面，在架子上拿咖啡，因为她生得矮小。")]), _vm._v(" "), _c('p', [_vm._v("“百顺——又往哪里跑？这点子工夫还惦记着玩！还不快触祭了上学去！”她叱喝。她那秀丽的刮骨脸凶起来像晚娘。")]), _vm._v(" "), _c('p', [_vm._v("百顺脸团团地，细眉细眼，陪着小心，把一张板凳搬到门外，又把一只饼干筒抱了出去，坐在筒上，凳上放了杯盘，静静等着。阿小从冰箱上的瓦钵子里拿出吃剩的半只大面包，说：")]), _vm._v(" "), _c('p', [_vm._v("“哪！拿去！有本事一个人把它全吃了！——也想着留点给别人。没看见的，这点大的小孩，吃得比大人还多！”")]), _vm._v(" "), _c('p', [_vm._v("窗台上有一只蓝玻璃杯，她把里面插着的牙刷拿掉了，热水瓶里倒出一杯水，递与百顺，又骂：“样样要人服侍！你一个月给我多少工钱，我服侍你？前世不知欠了你什么债！还不吃了快走！”")]), _vm._v(" "), _c('p', [_vm._v("百顺嘴里还在咀嚼，就去拿书包。突然，他对于他穿了一夏天的泛了灰的蓝布工人装感到十分疲倦，因此说：“姆妈，明天我好穿绒线衫了。”阿小道：“发什么昏！这么热的天，绒线衫！”")]), _vm._v(" "), _c('p', [_vm._v("百顺走了她叹了口气，想着孩子的学校真是难伺候。学费加得不得了，此外这样那样许多花头，单只做手工，红绿纸金纸买起来就吓人。窗台上，酱油瓶底下压着他做的一个小国旗，细竹签上挑出了青天白日满地红。阿小侧着头，看了一眼，心中只是凄凄惨惨不舒服。")]), _vm._v(" "), _c('p', [_vm._v("才把咖啡煮了，大银盘子端整好了，电话铃响起来。阿小拿起听筒，撇着洋腔锐声说：“哈罗？……是的密西，请等一等。”她从来没听见过这女人的声音。又是个新的。她去敲敲门：“主人，电话！”")]), _vm._v(" "), _c('p', [_vm._v("主人已经梳洗过了，穿上衣服了，那样子是很不高兴她。")]), _vm._v(" "), _c('p', [_vm._v("主人脸上的肉像是没烧熟，红拉拉的带着血丝子。新留着两撇小胡须，那脸蛋便像一种特别滋补的半孵出来的鸡蛋，已经生了一点点小黄翅。但是哥儿达先生还是不失为一个美男子。非常慧黠的灰色眼睛，而且体态风流。他走出来接电话，先咳嗽一声，可是喉咙里还有些混浊。他问道：“哈罗？”然后，突然地声音变得极其微弱：“哈罗哦！”又惊又喜，销魂地，等于说：“是你么？难道真的是你么？”他是一大早起来也能够魂飞魄散为情颠倒的。")]), _vm._v(" "), _c('p', [_vm._v("然而阿小，因为这一声迷人的“哈罗哦！”听过无数遍了，她自管自走到厨房里去。昨天“黄头发女人”请客，后来想必跟了他一起回来的，因为厨房里有两只用过的酒杯，有一只上面腻着口红。女人不知什么时候走的？他那些女人倒是从来不过夜的。女人去了之后他一个人到厨房里吃了个生鸡蛋，阿小注意到洋铁垃圾桶里有个完整的鸡蛋壳，他只在上面凿一个小针眼，一吸——阿小摇摇头，简直是野人呀！冰箱现在没有电，不应当关上的，然而他拿了鸡蛋顺手就关严了。她一开，里面冲出一阵甜郁的恶气。她取出乳酪，鹅肝香肠，一只鸡蛋。哥儿达除了一顿早饭在家里吃，其余两顿总是被请出去的时候多。冰箱里面还有半碗“杂碎”炒饭，他吃剩的，已经有一个多扎拜了。她晓得他并不是忘记了，因为他常常开冰箱打探情形的。他不说一声“不要了，你把它吃掉罢，”她也决不去问他“还要不要了？”她晓得他的脾气。")]), _vm._v(" "), _c('p', [_vm._v("主人挂上电话，检视备忘录上阿妈写下的，他不在家的时候人家打了来，留下的号码；照样打了去，却打不通。他伸头到厨房里，曼声叫：“阿妈，难为情呀！数目字老是弄不清楚！”竖起一只手指警戒地摇晃着。阿小两手包在围裙里，脸上露出于红的笑容。")]), _vm._v(" "), _c('p', [_vm._v("他向她孩子吃剩的面包瞟了一眼，阿小知道他起了疑心，其实这是隔壁东家娘有多余的面包票给了她一张，她去买了来的。主人还没有做声，她先把脸飞红了。苏州娘姨最是要强，受不了人家一点点眉高眼低的，休说责备的话了。尤其是阿小生成这一副模样，脸一红便像是挨了个嘴巴子，薄薄的面颊上一条条红指印，肿将起来。她整个的脸型像是被凌虐的，秀眼如同剪开的两长条，眼中露出一个幽幽的世界，里面“沉鱼落雁，闭月羞花”。")]), _vm._v(" "), _c('p', [_vm._v("主人心中想道：“再要她这样的一个人到底也难找，用着她一天，总得把她哄得好好的。”因此并不查问，只说：“阿妈，今天晚上预备两个人的饭。买一磅牛肉。”阿小说：“先煨汤，再把它炸一炸？”主人点点头。阿小说：“还要点什么呢？”主人沉吟着，一手支在门框上，一手撑腰；他那双灰色眼睛，不做媚眼的时候便翻着白眼，大而瞪，瞪着那块吃剩的面包，使阿小不安。他说：“珍珠米，也许？”她点头，说：")]), _vm._v(" "), _c('p', [_vm._v("“珍珠米。”每次都是同样的菜，好在请的是不同的女人，她想。他说：“还要一样甜菜，摊两个煎饼好了。”阿小道：“没有面粉。”他说：“就用鸡蛋，不用面粉也行。”甜鸡蛋阿小从来没听见过这样东西，但她还是熟溜地回答：“是的主人。”")]), _vm._v(" "), _c('p', [_vm._v("她把早饭送到房里去，看见小橱上黄头发女人的照片给收起来了。今天请的想必就是那新的女人，平常李小姐她们来他连照片也不高兴拿开。李小姐人最厚道，每次来总给阿小一百块钱。阿小猜她是个大人家的姨太太，不过也说不准，似乎太自由了些，而且不够好看——当然姨太太也不一定都好看。")]), _vm._v(" "), _c('p', [_vm._v("阿小又接了个电话：“哈罗？……是的密西，请等一等。”")]), _vm._v(" "), _c('p', [_vm._v("她敲门进去，说：“主人，电话。”主人问是谁。她说“李小姐。”主人不要听，她便替他回掉了：“哥儿达先生她在浴间里！”阿小只有一句“哈罗”说得最漂亮，再往下说就有点乱，而且男性女性的“他”分不大清楚。“对不起密西，也许你过一会再打来？”那边说：“谢谢。”她答道：“不要提。再会密西。”")]), _vm._v(" "), _c('p', [_vm._v("哥儿达先生吃了早饭出去办公，临走的时候照例在房门口柔媚地叫唤一声：“再会呀，阿妈！”只要是个女人，他都要使她们死心塌地喜欢他。阿妈也赶出来带笑答应：“再会主人！”她进去收拾房间，走到浴室里一看，不由得咬牙切齿恨了一声。哥儿达先生把被单枕套衬衫裤大小毛巾一齐泡在洗澡缸里，不然不放心，怕她不当天统统洗掉它。今天又没有太阳，洗了怎么得干？她还要出去买菜，公寓里每天只有一个钟头有自来水，浴缸被占据，就误了放水的时间，而他每天要洗澡的。")]), _vm._v(" "), _c('p', [_vm._v("李小姐又打电话来。阿小说：“哥儿达先生她去办公室！”")]), _vm._v(" "), _c('p', [_vm._v("李小姐改用中文追问他办公室的电话号码，阿小也改口说中文：“李小姐是吧？”笑着，满面绯红，代表一切正经女人替这个女人难为情。“我不晓得他办公室的电话什么号头。")]), _vm._v(" "), _c('p', [_vm._v("……")]), _vm._v(" "), _c('p', [_vm._v("他昨天没有出去。……是的，在家里吃晚饭的。……一个人吃的。今天不知道，没听见他说……“")]), _vm._v(" "), _c('p', [_vm._v("黄头发的女人打电话来，要把她昨天大请客问哥儿达借的杯盘刀叉差人送还给他。阿小说：“哥儿达先生她去办公室！")]), _vm._v(" "), _c('p', [_vm._v("……是的密西。我是阿妈。……我很好，谢谢你密西。“”黄头发女人“声音甜得像扭股糖，到处放交情，阿小便也和她虚情假意的、含羞带笑，仿佛高攀不上似的。阿小又问：“什么时候你派来阿妈？现在我去菜场，九点半回来也许。……")]), _vm._v(" "), _c('p', [_vm._v("谢谢你密西。……不要提，再会密西。“她逼尖了嗓子，发出一连串火炽的聒噪，外国话的世界永远是欢畅，富裕，架空的。")]), _vm._v(" "), _c('p', [_vm._v("她出去买了小菜回来。“黄头发女人”的阿妈秀琴，也是她自家的小姊妹，是她托哥儿达荐了去的，在后面拍门，叫：")]), _vm._v(" "), _c('p', [_vm._v("“阿姐！阿姐！”秀琴年纪不过二十一二，壮大身材，披着长长的鬈发也不怕热，蓝布衫上还罩着件玉绿兔子呢短大衣。能够打扮得像个大学女生，显然是稀有的幸运。就连她那粉嘟嘟的大圆脸上，一双小眼睛有点红红地睁不大开"), _c('span', {
