  }, [_vm._v("（不知是不是痧眼的缘故）")]), _vm._v("，好像她自己也觉得有一种鲜华，像蒙古妇女从脸上盖着的沉甸甸的五彩缨络缝里向外界窥视。")]), _vm._v(" "), _c('p', [_vm._v("阿小接过她手里报纸包的一大叠盘子，含笑问了一声：")]), _vm._v(" "), _c('p', [_vm._v("“昨天几点钟散的？”秀琴道：“闹到两三点钟。”阿小道：“东家娘后来到我们这里来了又回去，总天亮以后了。”秀琴道：")]), _vm._v(" "), _c('p', [_vm._v("“哦，后来还到这里来的？”阿小道：“好像来过的。”她们说到这些事情，脸上特别带着一种天真的微笑，好像不在说人的事情。她们那些男东家是风，到处乱跑，造成许多灰尘，女东家则是红木上的雕花，专门收集灰尘，使她们一天到晚揩拭个不了。她们所抱怨的，却不在这上头。")]), _vm._v(" "), _c('p', [_vm._v("秀琴两手合抱在胸前，看阿小归折碗盏，嘟囔道：“我们东家娘同这里的东家倒是天生的一对，花钱来得个会花，要用的东西一样也不舍得买。那天请客，差几把椅子，还是问对门借的。面包不够了，临时又问人家借了一碗饭。”阿小道：")]), _vm._v(" "), _c('p', [_vm._v("“那她比我们这一位还大方些。我们这里从来没说什么大请过客，请起来就请一个女人，吃些什么我说给你听：一块汤牛肉，烧了汤捞起来再煎一煎算另外一样。难末，珍珠米。")]), _vm._v(" "), _c('p', [_vm._v("客人要是第一次来的，还有一样甜菜，第二次就没有了。……")]), _vm._v(" "), _c('p', [_vm._v("他有个李小姐，实在吃不惯，菜馆里叫了菜给他送来。李小姐对他真是天地良心！他现在又搭上新的了。我看他一个不及一个，越来越不在乎了。今天这一个连哥儿达的名字都说不连牵。“秀琴道：“中国人么？“阿小点头，道：“中国人也有个几等几样……妹妹你到房里来看看李小姐送他的生日礼，一副银碗筷，晓得他喜欢中国东西，银楼里现打的，玻璃盒子装着，玻璃上贴着红寿字。“秀琴看着，啧啧叹道：")]), _vm._v(" "), _c('p', [_vm._v("“总要好几千？”阿小道：“不止！不止！”")]), _vm._v(" "), _c('p', [_vm._v("这时候出来一点太阳，照在房里，像纸烟的烟的迷迷的蓝。榻床上有散乱的彩绸垫子，床头有无线电，画报杂志，床前有拖鞋，北京红蓝小地毯，宫灯式的字纸篓。大小红木雕花几，一个套着一个。墙角挂一只京戏的鬼脸子。桌上一对锡蜡台。房间里充塞着小趣味，有点像个上等白俄妓女的妆阁，把中国一些枝枝叶叶衔了来筑成她的一个安乐窝。最考究的是小橱上的烟紫玻璃酒杯，各式各样，吃各种不同的酒；齐齐整整一列酒瓶，瓶口加上了红漆蓝漆绿漆的蛋形大木塞。")]), _vm._v(" "), _c('p', [_vm._v("还有浴室里整套的淡黄灰玻璃梳子，逐渐地由粗齿到细齿，七八只一排平放着，看了使人心痒痒的难过，因为主人的头发已经开始脱落了，越是当心，越觉得那珍贵的头发像眼睫毛似的，梳一梳就要掉的。")]), _vm._v(" "), _c('p', [_vm._v("墙上用窄银框子镶着洋酒的广告，暗影里横着个红头发白身子，长大得可惊的裸体美女，题着“一城里最好的”。和这牌子的威士忌同样是第一流。这美女一手撑在看不见的家具上，姿势不大舒服，硬硬地支拄着一身骨骼，那是冰棒似的，上面凝冻着冰肌。她斜着身子，显出尖翘翘的圆大乳房，夸张的细腰，股部窄窄的；赤着脚但竭力踮着脚尖仿佛踏在高跟鞋上。短而方的“孩儿面”，一双棕色大眼睛愣愣地望着画外的人，不乐也不淫，好像小孩子穿了新衣拍照，甚至于也没有自傲的意思；她把精致的乳房大腿蓬头发全副披挂齐整，如同时装模特儿把店里的衣服穿给顾客看。")]), _vm._v(" "), _c('p', [_vm._v("她是哥儿达先生的理想，至今还未给他碰到过。碰到了，他也不过想占她一点便宜就算了。如果太麻烦，那也就犯不着；他一来是美人迟暮，越发需要经济时间与金钱，而且也看开了，所有的女人都差不多。他向来主张结交良家妇女，或者给半卖淫的女人一点业余的罗曼史，也不想她们劫富济贫，只要两不来去好了。他深知“久赌必输”，久恋必苦的道理，他在赌台上总是看看风色，趁势捞了一点就带了走，非常知足。")]), _vm._v(" "), _c('p', [_vm._v("墙上挂着这照片式的画，也并不秽亵，等于展览着流线型的汽车，不买看看也好，阿小与秀琴都避免朝它看，不愿显得她们是乡下上来的，大惊小怪。")]), _vm._v(" "), _c('p', [_vm._v("阿小道：“趁着有水，我有一大盆东西要洗呢，妹妹你坐一歇。——天下就有这样痴心的女人！”她还在那里记挂李小姐，弯倒腰，一壁搓洗，一壁气喘吁吁说：“会得喜欢他！")]), _vm._v(" "), _c('p', [_vm._v("他一个男人，比十个女人还要小奸小坏。隔壁东家娘多下一张面包票，我领了一只面包来，他还当是他的，一双眼睛瞄法瞄法。偷东西也偷不到他头上！他呀，一个礼拜前吃剩下来一点饭还留到现在，他不说不要了，我也不动他的。‘上海这地方坏呀！中国人连佣人都会欺负外国人！’他要是不在上海，外国的外国人都要打仗去的，早打死了！——上次也是这样，一大盆衣服泡在水里，怕我不洗似的，泡得衬衫颜色落得一塌胡涂，他这也不说什么了——看他现在愈来愈烂污，像今天这个女人——怎么能不生病？前两个月就弄得满头满脸疖子似的东西，现在算好了，也不知抹的什么药，被单上稀脏。“")]), _vm._v(" "), _c('p', [_vm._v("秀琴半天没搭话，阿小回头看看，她倚在门上咬着指头想心思。阿小这就记起来，秀琴的婆家那边要讨了，她母亲要领她下乡去，她不肯。便问：“你姆妈还在上海么？”秀琴亲亲热热叫了一声“阿姐！”说道：“我烦死了在这里！”她要哭，水汪汪的温厚红润的眼睛完全像嘴唇了。")]), _vm._v(" "), _c('p', [_vm._v("阿小道：“我看你，去是要去的。不然人家说你，这么大的姑娘，一定是在上海出了花头。”秀琴道：“姆妈也这样说呀！去是要去的，去一去我就来，乡下的日子我过不惯！姆妈这两天起劲得很在那里买这样买那样，闹死了说贵，我说你叽咕些什么，棉被枕头是你自己要撑场面，那些绣花衣裳将来我在上海穿不出去的。我别的都不管，他们打的首饰里头我要一只金戒指。这点礼数要还给我们的。你看喏，他们拿只包金的来，你看我定规朝地下一掼！你看我做得出口伐？”")]), _vm._v(" "), _c('p', [_vm._v("她的尊贵骄矜使阿小略略感到不快。阿小同她的丈夫不是“花烛”，这些年来总觉得当初不该就那么住在一起，没经过那一番热闹。她说：“其实你将就些也罢了。不比往年——你叫他们哪儿弄金子去？”想说两句冷话也不行，伛偻在澡盆边，热得恍恍惚惚，口鼻之间一阵阵刺痛冒汗，头上的汗往下直流，抬手一抹，明知天热，还是诧异着。她蹲得低低的，秀琴闻得见她的黑拷绸衫上的汗味阵阵上升，像西瓜剖开来清新的腥气。")]), _vm._v(" "), _c('p', [_vm._v("秀琴又叹息。“不去是不行的了！他们的房子本来是泥地，单单把新房里装了地板……我心里烦得要死！听说那个人好赌呀——阿姐你看我怎么好？“")]), _vm._v(" "), _c('p', [_vm._v("阿小把衣服绞干了，拿到前面阳台上去晒，百顺放学回来，不敢揿铃，在后门口大喊：“姆妈！姆妈！”拍着木栅栏久久叫唤，高楼外，正午的太阳下，苍淡的大城市更其像旷野了。一直等阿小晾完了衣裳，到厨房里来做饭，方才听见了，开门放他进来，嗔道：“叽哩哇啦叫点什么？等不及似的！”")]), _vm._v(" "), _c('p', [_vm._v("她留秀琴吃饭，又来了两个客，一个同乡的老妈妈，常喜欢来同阿小谈谈天，别的时候又走不开，又不愿总是叨扰人家，自己带了一篮子冷饭，诚诚心心爬了十一层楼上来。还有个背米兼做短工的“阿姐”，是阿小把她介绍了给楼下一家洗衣服。她看见百顺，问道：“这就是你自己的那一个？”阿小对孩子叱道：“喊‘阿姨’！”慢回娇眼，却又脸红红的向朋友道歉似地说：“像个瘪三哦？”")]), _vm._v(" "), _c('p', [_vm._v("现在这时候，很少看得见阿小这样的热心留人吃饭的人。")]), _vm._v(" "), _c('p', [_vm._v("她爱面子，很高兴她今天刚巧吃的是白米饭。她忙着炒菜，老妈妈问起秀琴办嫁妆的细节。秀琴却又微笑着，难得开口，低着粉红的脸像个新嫁娘。阿小一一代她回答了，老妈妈也有许多意见。")]), _vm._v(" "), _c('p', [_vm._v("做短工的阿姐问道：“你们楼上新搬来的一家也是新做亲的？”阿小道：“嗳。一百五十万顶的房子，男家有钱，女家也有钱——那才阔呢！房子，家生，几十床被窝，还有十担米，十担煤，这里的公寓房子那是放也放不下！四个佣人陪嫁，一男一女，一个厨子，一个三轮车夫。”那四个佣人，像丧事里纸扎的童男童女，一个一个直挺挺站在那里，一切都齐全，眼睛黑白分明。有钱人做事是漂亮！阿小愉快起来——这样一说，把秀琴完全压倒了，连她的忧愁苦恼也是不足道的。")]), _vm._v(" "), _c('p', [_vm._v("阿姐又问：“结了亲几天了？”阿小道：“总有三天了罢？”"