，没有金戒指不嫁，许多排场。他时而答应一声“唔，”")]), _vm._v(" "), _c('p', [_vm._v("狡猾的黑眼睛望着茶，那微笑是很明白的，很同情的，使她伤心；那同情又使她生气，仿佛全是她的事——结婚不结婚本来对于男人是没什么影响的。同时她又觉得无味，孩子都这么大了，还去想那些。男人不养活她，就是明媒正娶一样也可以不养活她。谁叫她生了劳碌命。他挣的钱只够自己用，有时候还问她要钱去入会。")]), _vm._v(" "), _c('p', [_vm._v("男人旋过身去课子，指着教科书上的字考问百顺。阿小想起来，说：“我姆妈有封信来，有两句文话我不大懂。”“吴县县政府”的信封，“丁阿小女仕玉展”，左角还写着“呈祥”字样。男人看信，解释给她听：")]), _vm._v(" "), _c('p', {
