  }, [_vm._v("信通知。母在乡。一切智悉。近想女在沪。贵体康安。诸事迪吉。目下。女说。到十月。要下来。千吉。交女带点三日头药。下来。望你。收信。千定不可失误。者。乡下。近日。十分安乐。望女。不必远念。者再吾母。交女。一件。绒线衫。千定带下。不要望纪。倘有。不下来。速寄。有便之人。不可失约。余言不情。特此面谈可也。"), _c('br'), _vm._v(" "), _c('span', {
    staticClass: "sgntr"
  }, [_vm._v("九月十四日母王玉珍寄")])]), _vm._v(" "), _c('p', [_vm._v("乡下来的信从来没有提到过她的男人，阿小时常叫百顺代她写信回去，那边信上也从来不记挂百顺。念完了信，阿小和她男人都有点寂寥之感。男人默坐着，忽然为他自己辩护似地，说起他的事业：“除了做衣裳，我现在也做点皮货生意。目前的时世，不活络一点不行的。”他打开包袱，抖开两件皮大衣给她过目，又把个皮统子兜底掏出来，说：“所以海獭这样东西……”叙述海獭的生活习惯，原是说给百顺听。百顺撒娇撒痴，不知什么时候已经离开书本，偎在阿小身边，一只手伸到她衣服里找寻口袋，哼哼唧唧，纠缠不休。阿小非常注意地听她丈夫说话，听得出神：“唔……唔……哦哦……噢……嗳……”男人下了结论：“所以海里的东西真是奇怪。”")]), _vm._v(" "), _c('p', [_vm._v("阿小一时没有适当的对答，想了一想，道：“现在小菜场上乌贼很多了。”男人道：“唔。乌贼鱼这东西也非常奇怪。你没看见过大的乌贼，比人还大，一身都是脚爪，就像蜘蛛……”阿小皱起面皮，道：“真的么！吓死人了。”向百顺道：")]), _vm._v(" "), _c('p', [_vm._v("“呜哩呜哩吵点什么！……说什么！听不见！……发痴了！我哪里来五块钱给你！”然而她随即摸出钱来给了他。")]), _vm._v(" "), _c('p', [_vm._v("熨完了衣裳，阿小调了面粉摊煎饼，她和百顺名下的户口粉，户口糖。男人也有点觉得无功受禄，背着手在她四面转来转去，没话找话说。父子两个趁热先吃了，她还继续摊着。")]), _vm._v(" "), _c('p', [_vm._v("太阳黄烘烘照在三人脸上，后阳台的破竹帘子上飞来一只蝉，不知它怎么夏天过了还活着，趁热大叫：“抓！抓！抓！”")]), _vm._v(" "), _c('p', [_vm._v("响亮快乐地。")]), _vm._v(" "), _c('p', [_vm._v("主人回来了，经过厨房门口，探头进来柔声唤：“哈罗，阿妈！”她男人早躲到阳台上去了，负手看风景。主人花三千块钱雇了个人，恨不得他一回来她就驯鸽似地在他头上乱飞乱啄，因此接二连三不断地揿铃，忙得她团团转。她在冰箱里取冰，她男人立在她身后，低声说：“今天晚上我来。”阿小嫌烦似地说：“热死了！”她和百顺住的那个亭子间实在像个蒸笼。——但她忽然又觉得他站在她背后，很伶仃似的；他是不惯求人的——至于对她他从来没有求告过。……她面对着冰箱银灰色的肋骨，冰箱的构造她不懂，等于人体内脏的一张爱克斯光照片，可是这冰箱的心是在突突跳着，而里面喷出的一阵阵寒浪熏得她鼻子里发酸，要出眼泪了。她并不回头，只补上一句：“百顺还是让他在对过过夜好了。他们阿妈同小孩子都住在这里的。”男人说：“唔。”")]), _vm._v(" "), _c('p', [_vm._v("她送冰进房出来，男人已经去了。她下楼去拎了两桶水上来，打发主人洗了澡。门铃响，那新的女人如约来了。阿小猜是个舞女。她问道：“外国人在家么？”一路扭进房去。脑后一大圈鬈发撅出来老远，电烫得枯黄虬结，与其他部分的黑发颜色也不同，像个皮围脖子，死兽的毛皮，也说不上来这东西是死的是活的，一颤一颤，走一步它在后面跳一跳。")]), _vm._v(" "), _c('p', [_vm._v("阿小把鸡尾酒和饼干送进去。李小姐又来了电话。阿小回说主人不在家。李小姐这次忍不住有嗔怪的意思，质问道：")]), _vm._v(" "), _c('p', [_vm._v("“我早上打电话来你有没有告诉他？”阿小也生气了——从来还没有谁对于她的职业道德发生疑问，她淡淡地笑道：“我告诉他的呀！不晓得他可是忘记了呢！怎么，他后来没有打得来么？”李小姐顿了一顿，道：“没有呀，”声音非常轻微。阿小心想：谁叫你找上来的，给个佣人刻薄两句！但是她体念到李小姐每次给的一百块钱，就又婉媚地替哥儿达解释，随李小姐相信不相信，总之不使她太下不来台：“今天他本来起晚了，来不及地赶了出去，后来在行李间，恐怕又是忙，又是人多，打电话也不方便……”李小姐“唔，唔，”地答应着，却仿佛在那边哭泣着了。阿小道：“那么，等他回来了我再告诉他一声。”李小姐仿佛离得很远很远地，隐隐地道：“你也不要同他说了……”可是随即又转了口：“过天我有空再打来罢。”她仿佛连这阿妈都舍不得撒手似的，竟和她攀谈起来。")]), _vm._v(" "), _c('p', [_vm._v("她上次留心到，哥儿达的床套子略有点破了，他一个独身汉，诸事没人照管，她意思要替他制一床新的。阿小这时候也有点嫌这李小姐婆婆妈妈讨厌，又要替主人争面子，便道：“他早说了要做新的，因为这张床是顶房子时候顶来的，也不大合意，一直想重买一只大些的；如果就这只床上做了套子，尺寸又不对了。现在我替他连连，也看不出来了。”她对哥儿达突然有一种母性的卫护，坚决而厉害。")]), _vm._v(" "), _c('p', [_vm._v("正说着，哥儿达伸头出来探问，阿小忙向李小姐道：“听电梯响不晓得是不是他回来了呢！”一面按住听筒轻声告诉哥儿达。哥儿达皱了皱眉，走出来了，却向里指指，叫阿小进去把酒杯茶点收出来。他接过听筒，且不坐下来，只望墙上一靠，叉着腰，戒备地问道：“哈罗？……是的，这两天忙。……不要发痴！哪有的事？”那边并没有炸起来，连抽搭抽搭的哭声也一口气吸了进去听不见了。他便消闲下来，重又低声笑道：“不要发痴了……你好么？“正好呢喃耳语着，万一房里那一个在那里注意听。”你那股票我已经托他买了。看你的运气！这一向头痛毛病没有发么？睡得还好？“他向电话里”嘘！嘘！“吹口气，使那边耳朵里一阵奇痒。也许他从前常在她耳根下吹口气作耍的，两人都像是旧梦重温，格格地笑起来。他又道：“那么，几时可以看见你呢？“说到幽会，是言归正传，他马上声音硬化起来，丁是丁，卯是卯的。")]), _vm._v(" "), _c('p', [_vm._v("“星期五怎么样？……这样好不好，先到我这里来再决定。”如果先到他这里来，一定就是决定不出去了，在家吃晚饭。他一只手整理着卷曲的电话线，一壁俯身去看桌上一本备忘簿上阿妈写下来的，记错了的电话号码——她总是把９字写反过来。")]), _vm._v(" "), _c('p', [_vm._v("是谁打了来的呢？不会是……但这阿妈真是恼人！他粗声回答电话里：“……不，今天我要出去。我现在不过回来换件衣服就要走的。……”然而他又软了下来，电话上谈到后来应当是余音袅袅的。他道：“所以……那么，一直要到星期五！”")]), _vm._v(" "), _c('p', [_vm._v("微喟着。叮咛着：“当心你自己。拜拜，甜的！”末了一句仿佛轻轻的一吻。")]), _vm._v(" "), _c('p', [_vm._v("阿小进去收拾阳台上一张藤桌上的杯盏，女人便倚着铁栏杆。对于这年轻的舞女，这一切都是新鲜浪漫的罢？傍晚的城中起了一层白雾，雾里的黄包车紫阴阴地远远来了，特别地慢，慢慢过去一辆；车灯，脚踏车的铃声，都收敛了，异常轻微，仿佛上海也是个紫禁城。")]), _vm._v(" "), _c('p', [_vm._v("楼下的阳台伸出一角来像轮船头上。楼下的一个少爷坐在外面乘凉，一只脚蹬着栏干，椅子向后斜，一晃一晃，而不跌倒，手里捏一份小报，虽然早已看不见了。天黑了下来；地下吃了一地的柿子菱角。阿小恨不得替他扫扫掉——上上下下都是清森的夜晚，如同深海底。黑暗的阳台便是载着微明的百宝箱的沉船。阿小心里很静也很快乐。")]), _vm._v(" "), _c('p', [_vm._v("她去烧菜，油锅拍辣辣爆炸，她忙得像个受惊的鸟，扑来扑去。先把一张可以折叠的旧式大菜台搬进房去，铺上台布，汤与肉先送进去，再做甜菜。甜鸡蛋到底不像话，她一心软，给他添上点户口面粉，她自己的，做了鸡蛋饼。")]), _vm._v(" "), _c('p', [_vm._v("她和百顺吃的是菜汤面疙瘩，一锅淡绿的粘糊，嘟嘟煮着，面上起一点肥胖的颤抖。百顺先吃完了，走到后阳台上，一个人自言自语：“月亮小来！星少来！”")]), _vm._v(" "), _c('p', [_vm._v("阿小诧异道：“瞎说点什么？”笑起来了，“什么‘月亮小来，星少来’？发痴滴搭！”")]), _vm._v(" "), _c('p', [_vm._v("她进去收拾碗盏，主人告诉她：“待会儿我们要出去。你等我们走了，替我铺了床再走。”阿小答应着，不禁罕异起来——这女人倒还有两手，他仿佛打算在她身上多花几个钱似的！")]), _vm._v(" "), _c('p', [_vm._v("她想等临走的时候再把百顺交给对过的阿妈，太早了怕他们嫌烦。烧开了两壶水，为百顺擦脸洗脚，她自己也洗脚，洗脖颈。电话铃响，她去接：“哈罗？”那边半天没有声音。")]), _vm._v(" "), _c('p', [_vm._v("她猜是个中国人打错了的，越发仿着个西洋悍妇的口吻，火高三丈锐叫一声“哈罗？”那边怯怯地说：“喂？阿妈还在吗？”")]), _vm._v(" "), _c('p', [_vm._v("原来是她男人，已经等了她半天了。“十点钟了，”他说。")]), _vm._v(" "), _c('p', [_vm._v("阿小听听主人房里还是鸦雀无声。百顺坐在饼干筒上盹着了。下起雨来了，竹帘子上淅沥淅沥，仿佛是竹竿梦见了它们自己从前的叶子。她想：“这样子倒好，有了个借口。”她喊醒了百顺，领他走到隔壁去，向对过阿妈解释：“下雨，不带他回去了，小人怕他滑跌跤，又喜欢伤风，跟着阿姨睡一晚罢！”回到这边来，主人还是没有动静，她火冒起来，敲门没人理，把门轻轻推开一线，屋里漆黑的，不知什么时候已经双双出去了。阿小忍着气，替他铺了床。她自己收拾回家，拿了钥匙网袋雨伞，短大衣舍不得淋湿，反折着挽在手里，开后门下楼去。")]), _vm._v(" "), _c('p', [_vm._v("雨越下越大。天忽然回过脸来，漆黑的大脸，尘世上的一切都惊惶遁逃，黑暗里拼铃碰隆，雷电急走。痛楚的青，白，紫，一亮一亮，照进小厨房，玻璃窗被逼得往里凹进去。")]), _vm._v(" "), _c('p', [_vm._v("阿小横了心走过两条马路，还是不得不退回来，一步拖一步走上楼来，摸到门上的锁，开了门，用网袋包着手开了电灯，头上身上黑水淋漓。她把鞋袜都脱了，白缎鞋上绣的红花落了色，红了一鞋帮。她挤掉了水，把那双鞋挂在窗户钮上晾着。光着脚踏在砖地上，她觉得她是把手按在心上，而她的心冰冷的像石板。厨房内外没有一个人，哭出声来也不要紧。")]), _vm._v(" "), _c('p', [_vm._v("她为她自己突如其来的癫狂的自由所惊吓，心里模糊地觉得不行，不行！不能一个人在这里，快把百顺领回来罢。")]), _vm._v(" "), _c('p', [_vm._v("她走到隔壁去，幸喜后门口还没上闩，厨房里还点着灯。她一直走进去，拍拍玻璃窗，哑着喉咙叫：“阿姐！开开门！”对过阿妈道：“咦？你还没回去么？”阿小带笑道：“不好走呀！")]), _vm._v(" "), _c('p', [_vm._v("雨太大，现在这断命路又没有灯！马路上全是些坑，坑里全是水——真要命！想想还是在这里过夜罢。我那瘪三困了没有？还是让他跟我睡去罢。“")]), _vm._v(" "), _c('p', [_vm._v("对过阿妈道：“你有被头在这里么？”阿小道：“有的有的。”")]), _vm._v(" "), _c('p', [_vm._v("她把棉被铺在大菜台上，下面垫了报纸，熄了灯，与百顺将就睡下。厨房里紧小的团圆暖热里生出两只苍蝇来，在头上嗡嗡飞鸣。雨还是哗哗大下。忽地一个闪电，碧亮的电光里又出了一个蜘蛛，爬在白洋瓷盆上。")]), _vm._v(" "), _c('p', [_vm._v("楼上的新夫妇吵起嘴来了，訇訇响，也不知是蹬脚，还是人被推操着跌到橱柜或是玻璃窗上。女人带着哭声哩哩罗罗讲话，仿佛是扬州话的“你打我！……你打我！……你打死我啊！”阿小在枕上倾听，心里想：“一百五十万顶了房子来打架！才结婚了三天，没有打架的道理呀！……除非是女人不规矩……”她朦胧中联想到秀琴的婆家已经给新房里特别装上地板，秀琴势不能不嫁了。")]), _vm._v(" "), _c('p', [_vm._v("楼上闹闹停停，又闹起来。这一次的轰轰之声，一定是女人在那里开玻璃门，像是要跳楼，被男人拖住了。女人也不数落了，只是放声嚎哭。哭声渐低，户外的风雨却潮水似地高起来，呜呜叫嚣，然后又是死寂中的一阵哭闹，再接着一阵风声雨声，各不相犯，像舞台上太显明地加上去的音响效果。")]), _vm._v(" "), _c('p', [_vm._v("阿小拖过绒线衫来替百顺盖盖好，想起从前同百顺同男人一起去看电影，电影里一个女人，不知怎么把窗户一推，就跨了出去；是大风雨的街头，她歪歪斜斜在雨里奔波，无论她跑到哪里，头上总有一盆水对准了她浇下来。阿小苦恼地翻了个身，在枕头那边，雨还是哗哗下，一盆水对准了她浇下来。她在雨中睡着了。")]), _vm._v(" "), _c('p', [_vm._v("将近午夜的时候，哥儿达带了女人回来，到厨房里来取冰水。电灯一开，正照在大菜台上，百顺睡梦里唔唔呻吟，阿小醒了，只做没醒。她只穿了件汗衫背心，条纹布短裤，侧身向里，瘦小得像青蛙的手与腿压在百顺身上，头上的两只苍蝇，叮叮地朝电灯泡上撞。哥儿达朝她看了一眼。这阿妈白天非常俏丽有风韵的，卸了装却不行。他心中很觉安慰，因为他本来绝对没有沾惹她的意思；同个底下人兜搭，使她不守本分，是最不智的事。何况现在特殊情形，好的佣人真难得，而女人要多少有多少。")]), _vm._v(" "), _c('p', [_vm._v("哥儿达捧了一玻璃盆的冰进去。女人在房里呵呵笑着，她喝下的许多酒在人里面晃荡晃荡，她透明透亮地成了个酒瓶，香水瓶，躺在一个盒子的淡绿碎鬈纸条里的贵重的礼物。门一关，笑声听不见了，强烈的酒气与香水香却久久不散。厨下的灯灭了，苍蝇又没头没脑扑上脸来。")]), _vm._v(" "), _c('p', [_vm._v("雨仿佛已经停了好一会。街上有人慢悠悠叫卖食物，四个字一句，不知道卖点什么，只听得出极长极长的忧伤。一群酒醉的男女唱着外国歌，一路滑跌，嘻嘻哈哈走过去了；沉沉的夜的重压下，他们的歌是一种顶撞，轻薄，薄弱的，一下子就没有了。小贩的歌，却唱彻了一条街，一世界的烦忧都挑在他担子上。")]), _vm._v(" "), _c('p', [_vm._v("第二天，阿小向开电梯的打听楼上新娘子为什么半夜三更寻死觅活大闹。开电梯的诧异道：“哦？有这事么？今天他们请客，请女家的人，还找了我去帮忙哩。”还是照样地请了客。")]), _vm._v(" "), _c('p', [_vm._v("阿小到阳台上晾衣服，看见楼下少爷昨晚乘凉的一把椅子还放在外面。天气骤冷，灰色的天，街道两旁，阴翠的树，静静的一棵一棵，电线杆一样，没有一点胡思乱想。每一株树下团团围着一小摊绿色的落叶，乍一看如同倒影。")]), _vm._v(" "), _c('p', [_vm._v("乘凉仿佛是隔年的事了。那把棕漆椅子，没放平，吱格吱格在风中摇，就像有个标准中国人坐在上头。地下一地的菱角花生壳，柿子核与皮。一张小报，风卷到阴沟边，在水门汀栏杆上吸得牢牢地。阿小向楼下只一瞥，漠然想道：天下就有这么些人会作脏！好在不是在她的范围内。")]), _vm._v(" "), _c('p', {
